

import {
    withStyles,
    makeStyles,
    createStyles,
    TextField,
    Typography,
} from "@material-ui/core";
import React, { Component } from "react";
import TextareaAutosize from "@material-ui/core/TextareaAutosize";
//import PropTypes from "prop-types";

import DoneIcon from "@material-ui/icons/Done";
import CardContent from "@material-ui/core/CardContent";
import Card from "@material-ui/core/Card";

const useStyles = (theme) => ({
    root: {
        "& > *": {
            margin: theme.spacing(1),
            width: "25ch",
        },
        "& .MuiFilledInput-root": {
            background: "#eee",
        },
    },
    buttonStyle: {
        "& .MuiFilledInput-root": {
            background: "#3F51B5",
            color: "#ffffff",
            borderRadius: "10px",
            borderStyle: "none",
            focus: "none",
            /**the input field property always override here  */
        },

        borderStyle: "none",
        align: "center ",

        margin: "2em 0",

        float: "right",
        marginRight: "5em",
        width: "40%",
    },
    emailBoxStyling: {
        width: "80%",
        margin: "2em 0",
        "& .MuiFilledInput-root": {
            /**to override the primary color of the material ui component */
            background: "#E7E7E7",
        },
    },
    commentFieldStyling: {
        outline: "none",
        width: "80%",
        margin: "2em 0",
        background: "#E7E7E7",
    },
});


class FeedBack extends Component {
    state = {
        view: { display: "none" },
        error: "",
        email: "",
        feedback: "",
    };
    onChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value,
        });
    };

    onSubmit = (e) => {
        e.preventDefault();

    };
    render() {
        const { classes } = this.props;
        return (


            <div style={parentDivStyling}>

                <Card><CardContent>
                    <div style={{ width: "100%", margin: "2em 0", textAlign: "center" }}>
                        <Typography variant="h5">FeedBack</Typography>
                    </div>


                    <form
                        className={classes.root}
                        noValidate
                        autoComplete="off"
                        onSubmit={this.onSubmit}
                        style={stylingForm}>
                        <TextField
                            id="filled-basic"
                            label="email"
                            variant="outlined"
                            name="email"
                            value={this.state.email}
                            onChange={this.onChange}
                            className={classes.emailBoxStyling}
                        />
                        {/* <DoneIcon color="primary" style={this.state.view.email} /> */}
                        <div>
                            <span style={{ color: "red" }}>{this.state.error}</span>
                        </div>
                        <TextareaAutosize
                            id="feedback"
                            minRows={15}
                            aria-label="maximum height"
                            placeholder="write a comment"
                            name="feedback"
                            onChange={this.onChange}
                            value={this.state.feedback}
                            className={classes.commentFieldStyling}
                        />
                        {/* <DoneIcon color="primary" style={this.state.view.feedback} /> */}

                        <TextField
                            name="submit"
                            id="submit"
                            variant="filled"
                            type="submit"
                            value="Submit"
                            size="small"
                            className={classes.buttonStyle}
                        />
                    </form>

                </CardContent></Card>
            </div>
        );
    }
}

const stylingForm = {
    marginLeft: "10%",
};

const parentDivStyling = {
    width: "100%",
    display: "flex",
    margin: "auto",
    justifyContent: "center",
    flexDirection: "column",
    background: "#eee",
};

// FeedBack.PropTypes = {
// 	onSubmit: PropTypes.func.isRequired,
// };

export default withStyles(useStyles, { withTheme: true })(FeedBack);
